/* TaigrTab.css */
.taigrtab-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #333; /* Dark background color */
  color: #fff; /* Light text color */
  border-radius: 10px;
}

.title {
  text-align: center;
  font-size: 2.5rem;
  color: #902bf5; /* Accent color */
  margin-bottom: 30px;
}

.section {
  margin-bottom: 30px;
}

.section h2 {
  font-size: 1.8rem;
  color: #902bf5; /* Accent color */
  border-bottom: 2px solid #902bf5; /* Accent color */
  padding-bottom: 5px;
  margin-bottom: 15px;
}

.section p {
  font-size: 1.1rem;
  line-height: 1.6;
}

.section ul,
.section ol {
  font-size: 1.1rem;
  margin-top: 10px;
}

.section ul li,
.section ol li {
  margin-bottom: 10px;
}

.section a {
  color: #902bf5; /* Accent color */
  text-decoration: none;
  transition: color 0.3s ease;
}

.section a:hover {
  color: #0056b3; /* Darker accent color on hover */
}

.contact ul {
  list-style: none;
  padding: 0;
}

.contact ul li {
  margin-bottom: 10px;
}

.contact ul li a {
  display: inline-block;
  padding: 5px 0;
  color: #902bf5; /* Accent color */
  transition: color 0.3s ease;
}

.contact ul li a:hover {
  color: black; /* Darker accent color on hover */
}
