/* Taigrbot.css */
.taigrbot {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff; /* White background */
    color: #333; /* Black text color */
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /* Shadow for container */
  }
  
  .taigrbot-header {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .taigrbot-header h2 {
    font-size: 2.5rem;
  }
  
  .tagline {
    color: #666; /* Medium grey text color */
  }
  
  .taigrbot-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .feature {
    text-align: center;
    padding: 20px;
    background-color: #f0f0f0; /* Light grey background */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Shadow for feature boxes */
  }
  
  .custom-icon {
    width: 60px;
    height: 60px;
    margin: 0 auto 10px;
    border-radius: 50%;
    background-size: cover;
  }
  
  .music-icon {
    background-color: #9b59b6; /* Soft purple for music icon */
  }
  
  .shield-icon {
    background-color: #3498db; /* Blue for shield icon */
  }
  
  .tasks-icon {
    background-color: #2ecc71; /* Green for tasks icon */
  }
  
  .smile-icon {
    background-color: #f39c12; /* Orange for smile icon */
  }
  
  .bot-add {
    text-align: center;
    margin-top: 30px;
  }
  
  .bot-add h3 {
    margin-bottom: 10px;
  }
  
  .bot-add ol {
    padding-left: 20px;
  }
  
  .bot-add ol li {
    margin-bottom: 10px;
  }
  
  .bot-add a {
    color: #007bff; /* Blue color for links */
    text-decoration: none;
  }
  
  .bot-add a:hover {
    text-decoration: underline;
  }
  
  .github-section {
    margin-top: 30px;
    text-align: center;
  }
  
  .github-section h3 {
    margin-bottom: 10px;
  }
  
  .github-section p {
    color: #666; /* Medium grey text color */
  }
  
  .github-section a {
    display: block;
    color: #007bff; /* Blue color for links */
    text-decoration: none;
    margin-top: 10px;
  }
  
  .github-section a:hover {
    text-decoration: underline;
  }
  
  .commands-section {
    margin-top: 30px;
    text-align: center;
  }
  
  .commands-section h3 {
    margin-bottom: 10px;
  }
  
  .commands-section p {
    color: #666; /* Medium grey text color */
  }
  
  .command-list {
    list-style: none;
    padding: 0;
  }
  
  .command-list li {
    margin-bottom: 10px;
  }
  
  .command-list code {
    background-color: #f0f0f0; /* Light grey background for code */
    padding: 2px 5px;
    border-radius: 4px;
  }
  
  /* Styling for code elements */
code {
  background-color: #2d2d2d;
  color: #f8f8f2;
  padding: 2px 4px;
  border-radius: 3px;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.1em;
  margin: 0 2px;
}
