.whoami-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
    background-color: #2e2e2e;
    color: #902bf5;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .header {
    text-align: center;
    margin-bottom: 20px;
    color:#902bf5;
  }

  .header2 {
    text-align: center;
    margin-bottom: 20px;
    color: orangered;
    font-size: 2em;
    animation: scale 2s ease-in-out infinite alternate, glow 2s linear infinite alternate;
  }
  
  @keyframes scale {
    0%, 100% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.1);
    }
  }
  
  @keyframes glow {
    0% {
      text-shadow: 0 0 10px rgba(144, 43, 245, 0.8), 0 0 20px rgba(144, 43, 245, 0.8), 0 0 30px rgba(144, 43, 245, 0.8);
    }
    100% {
      text-shadow: 0 0 20px rgba(144, 43, 245, 0.8), 0 0 30px rgba(144, 43, 245, 0.8), 0 0 40px rgba(144, 43, 245, 0.8);
    }
  }
  
  .introduction-section,
  .skills-section,
  .projects-section,
  .contact-section {
    margin-bottom: 20px;
    background: #3a1e58;
    color:orangered;
    padding: 20px;
    border-radius: 8px;
  }
  
  .skills-list,
  .projects-list,
  .contact-list {
    list-style-type: none;
    padding: 0;
  }
  
  .skills-list li,
  .projects-list li,
  .contact-list li {
    color: orangered;
    background: #000000;
    border: 1px solid #902bf5;
    padding: 10px;
    margin: 5px 0;
    border-radius: 8px;
  }
  
  .contact-list a {
    color: #902bf5;
    text-decoration: none;
  }
  
  .contact-list a:hover {
    color:black;
  }