/* src/css/Navbar.css */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
}

.navbar {
  background: #1a1a1a;
  color: #fff;
  padding: 0 20px;
  position: relative;
  z-index: 10;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  position: relative;
}

ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  transition: transform 0.3s ease-in-out;
}

li {
  margin: 0 10px;
  color: white;
}

a {
  color: #fff;
  text-decoration: none;
  padding: 10px 15px;
  border-radius: 4px;
  transition: background 0.3s ease, color 0.3s ease;
}

a:hover, .active {
  background: #902bf5; /* Purple background on hover and active */
  color: orangered;
}

.dropdown {
  position: relative;
}

.dropdown-menu {
  display: none;
  position: absolute;
  background: #444;
  top: 40px;
  left: 0;
  min-width: 150px;
  list-style: none;
  padding: 10px 0;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.dropdown-menu li {
  margin: 0;
}

.dropdown-menu a {
  display: block;
  padding: 10px 20px;
  transition: background 0.3s ease;
}

.dropdown-menu a:hover {
  background: #555;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.mobile-menu-icon {
  display: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #fff;
  position: absolute;
  right: 20px;
}

.nav-links {
  display: flex;
  align-items: center;
}

.nav-links-mobile {
  display: none;
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
  }

  .mobile-menu-icon {
    display: block;
  }

  .nav-links-mobile {
    display: flex;
    flex-direction: column;
    width: 100%;
    background: #1a1a1a;
    position: absolute;
    top: 60px;
    left: 0;
    padding: 20px 0;
    align-items: center;
    transform: translateY(-100%);
    transition: transform 0.3s ease-in-out;
  }

  .nav-links-mobile li {
    margin: 10px 0;
  }

  .nav-links-mobile li a {
    padding: 10px 20px;
  }

  .nav-links-mobile.open {
    transform: translateY(0);
  }
}
