/* src/css/Header.css */
.header {
  text-align: center;
  margin: 40px 0;
}

.animated-title {
  font-size: 3rem;
  color: #902bf5; /* Purple color for the title */
  position: relative;
  animation: fadeIn 2s ease-in-out, moveIn 1.5s ease-in-out;
  display: inline-block;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes moveIn {
  0% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
  }
}

.animated-title::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  background: #902bf5; /* Purple color for the underline */
  left: 0;
  bottom: -10px;
  animation: underline 2s ease-in-out;
}

@keyframes underline {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
